button {
  outline: none;
  border: none;
}

button:active {
  opacity: 0.7;
  transform: scale(1.1, 1.1);
}

#user-input-container {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#input-bar {
  width: 0rem;
  height: 0rem;
  opacity: 0;
}

.go-button {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: large;
  font-weight: 400;
  width: 35rem;
  height: 3rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  color: #ffffff;
  background-color: #56b1b1;
  border-radius: 0.5rem;
  transition: width 100ms ease-in-out, color 100ms linear;
  overflow: hidden;
}

.go-button.error {
  /* animation: shake 500ms linear 0 1; */
  animation: 300ms shake linear 1;
  background-color: #fe5f57;
}

.go-button:hover {
  opacity: 0.9;
  cursor: pointer;
}

#mirrored-input {
  white-space: pre;
  color: #ffffff;
  margin-right: 0.1rem;
  opacity: 0.8;
}

#mirrored-input::after {
  content: "";
  background-color: white;
  position: absolute;
  top: 1rem;
  width: 0.2rem;
  height: 1rem;
  animation: 1.25s blink ease-in infinite;
}

#go-button-icon {
  min-width: 2rem;
  min-height: 1rem;
}

#word-count {
  position: absolute;
  right: 0;
  bottom: -50%;
  color: white;
  font-size: medium;
  opacity: 0.5;
}

.user-interact-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.rate-bar {
  position: absolute;
  right: 0;
  top: -100%;
  width: 6rem;
  height: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 0.5rem;
}

.rate-option {
  height: 0.75rem;
  width: 0.75rem;
  top: 0;
  right: 0;
  border-radius: 2rem;
  z-index: 2;
  /* transition-property: width, height, background-color;
  transition-duration: 250ms;
  transition-timing-function: ease-in-out; */
  transition: width 150ms ease-in-out, height 150ms ease-in-out,
    background-color 250ms linear 150ms;
  display: flex;
  justify-content: center;
  align-items: center;
}

.option-icon {
  opacity: 0;
  position: relative;
  top: 0;
  left: 0;
}

.rate-option:hover {
  position: relative;
  overflow: visible;
  cursor: pointer;
  height: 5rem;
  width: 5rem;
  border-radius: 3rem;
  z-index: 3;
}

.rate-option:hover .option-icon {
  position: relative;
  opacity: 1;
}

.rate-option:hover#like-option,
.rate-option:hover#dislike-option,
.rate-option:hover#report-option {
  background-color: rgba(0, 0, 0, 0);
}

#like-option {
  background-color: #27c840;
}

#dislike-option {
  background-color: #fe5f57;
}

#report-option {
  background-color: #ffbc30;
}

.error-message {
  position: absolute;
  text-align: center;
  width: 35rem;
  height: 2rem;
  overflow: visible;
  color: #fe5f57;
  bottom: -200%;
  transition: opacity 500ms ease-out;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes shake {
  25% {
    transform: translateX(-60%);
  }
  50% {
    transform: translateX(50%);
  }

  60% {
    transform: translateX(-40%);
  }
  85% {
    transform: translateX(30%);
  }

  90% {
    transform: translateX(-20%);
  }

  95% {
    transform: translate(10%);
  }
  100% {
    transform: translateX(0);
  }
}
