body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: large;
  font-weight: 300;
  font-style: italic;
}

#root {
  width: 100%;
  height: 100%;
}

.App {
  background-color: #22273a;
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.name-input-screen {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.name-input-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.name-input-prompt {
  display: block;
  color: white;
  margin-bottom: 3rem;
  text-align: center;
}

.name-input-error {
  color: red;
  margin-top: 3rem;
  text-align: center;
}

#name-input {
  border-radius: 0.5rem;
  font-size: large;
}
#name-input:focus {
  outline: none;
  font-size: larger;
}

.name-display {
  color: white;
  position: fixed;
  top: 1rem;
  right: 1rem;
}

.answers-tab {
  background-color: rgba(255, 255, 255, 0.1);
  opacity: 1;
  width: 99vw;
  height: 40vh;
  position: fixed;
  bottom: -30%;
  border-radius: 1rem 1rem 0 0;
  transition: bottom 200ms ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 1rem;
  padding-top: 1rem;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 10;
}

.answers-tab:hover {
  bottom: 0;
}

.answer-info {
  color: #ffbc30;
  opacity: 0.7;
}

.response-bar {
  height: 2rem;
  border-radius: 1rem;
  opacity: 0.7;
}

.likes-bar {
  position: relative;
  background-color: #27c840;
  bottom: 2rem;
  align-self: flex-start;
  justify-self: flex-start;
}

.dislikes-bar {
  background-color: #fe5f57;
}

.question-response {
  margin: 2rem;
  color: white;
  opacity: 0.7;
}

.reload-button {
  min-width: 3rem;
  min-height: 3rem;
  color: white;
  position: fixed;
  right: 0;
  margin: 1rem;
  cursor: pointer;
}

.reload-button.disabled {
  pointer-events: none;
  opacity: 0.1;
}
